import React from 'react';
import { withScriptjs, withGoogleMap, GoogleMap, Marker } from 'react-google-maps';

const Map = withScriptjs(withGoogleMap((props) => {
  const placeMarkers = props.isOneDestination ? <Marker position={props.destinations.geometry.location}></Marker> : 
    props.destinations.map( dest => <Marker
        key={dest.uid}
        position={{lat: dest.lat, lng: dest.lng}}
        //icon={props.destinations.length===1? } //fix this to change the icon based on store or restaurant etc.
    />);
  const friendMarkers = props.friends.map( friend => <Marker
    key={friend.id}
    position={{lat: friend.lat, lng: friend.lng}}
  />);
  console.log("is one destination? ", props.isOneDestination)
  console.log("destination position", props.destinations.geometry.location)
  return (
    
    <GoogleMap
      defaultZoom={12}
      center={{lat: 38.895, lng: -77.036}}
      >
      {props.destinations.length && placeMarkers}
      {props.friends.length && friendMarkers }
    </GoogleMap>
    
  );

  }
))

export default Map
