import React, { useState} from 'react'
import MapContainer from './MapContainer'
import {CalculateContainer, CalculateSidebar, PlaceTitle, PlaceDescription, Title} from '../styles'
import FriendContainer from './FriendContainer'
import SearchBoxContainer from './Searchbox'

const Calculate = (props) => {
    //const [addr, setAddr] = useState([])
    const [places, setPlaces] = useState([])
    const [destination, setDestination] = useState({}) //hold latitude and longitude
    const [friends, setFriends] = useState([])
    return(
    <>
        <CalculateContainer>

                <CalculateSidebar>
                    <Title>Where to meet?</Title>
                    <SearchBoxContainer value={destination} setValue={(newPlace) => setDestination(newPlace)}></SearchBoxContainer>
                </CalculateSidebar>
                <CalculateSidebar>
                    {/* <PlaceTitle style={{fontSize: '36px'}}>{JSON.parse(props.location.data).name}</PlaceTitle>
                    {console.log(props.location.data)}
                    <PlaceDescription> {JSON.parse(props.location.data).formatted_address}</PlaceDescription> */}
                    <FriendContainer destination={destination}
                        value={friends} setValue={setFriends}
                    >
                    </FriendContainer>
                </CalculateSidebar>
            
        <MapContainer friends={friends} destinations={places.length ? places : destination} isOneDestination={places.length}></MapContainer>
        </CalculateContainer>

    </>
    )
}
export default Calculate