import React, {useReducer} from 'react'
import {nanoid} from 'nanoid'
import { FriendTitle, AddButton, FriendItem, FriendListStyle, FriendContainerStyle, FriendName, DeleteButton} from '../styles'
import SearchBoxContainer from './Searchbox'
import DeleteImg from '../images/delete.png'
//port { parseSync } from '@babel/core'

//based on personal-assistant, spread operator includes+adds each item in list
const formReducer = (prevState, action) => {
  console.log("formReducer called "+JSON.stringify(prevState)+" "+JSON.stringify(action));
  if(!action){
    return { ...prevState }
  }
  switch(action.type){
    case 'NEW_NAME':
      var i;
      for (i = 0; i < prevState['friends'].length; i++) {
          if(prevState['friends'][i].id === action.id)
            prevState['friends'][i].name = action.name;
      }
      return prevState;
    case 'NEW_ADDRESS':
        console.log("new address case in formReducer")
        var i;
        for (i = 0; i < prevState['friends'].length; i++) {
            if(prevState['friends'][i].id === action.id)
              prevState['friends'][i].address = action.address;
        }
        console.log(prevState['friends']);
        console.log(prevState);
        console.log("action destination", action.destination.geometry.location)
        return prevState;
    default:
      return { ...prevState, ...action }
  }
}

const distance = require('google-distance-matrix')

const getDistance = (friends, destinations, travelMode) => {
  console.log("Getting distance from API...")
  let origins = friends.map(friend => friend.address)
  console.log("origins", origins[0])
  // axios.post('/api/distance', {origins, destinations, travelMode})
  //   .then(res => res.data)
  //   .then(data => this.props.setDist(data))
  //   .catch(err => console.log(`Unable to get distances, ${err}`))
  distance.key('AIzaSyAxMUZv8ivMGNhpxGY4czVdEU-OC80CXeA')
  distance.mode(travelMode)
  distance.avoid('tolls')
  distance.matrix(origins, [destinations], function (err, distances) {
    if (!err) {
      console.log("Distance API call recieved ", distances);
      friends.forEach((friend, index) => {
        const dist = distances[index];
          friend.distance = dist;
      });
    } else {
      console.log(`Unable to get distances, ${err}`);
    }
  })
}

const FriendContainer = ({destination}) => {
  const [form, setForm] = useReducer(formReducer, {friends: []})

  return(
    <FriendContainerStyle>
    <div style={{display:"flex", justifyContent:"space-between"}}>
    <FriendTitle>Friends</FriendTitle>
    <AddButton onClick={()=>setForm(form['friends'].push({id:nanoid(), name: "Sample Name", address: "Enter an address...", distance: ""}))}>+</AddButton> 
    </div>
    {form.friends.length>0? <FriendList 
        // t={ form['tasks'].filter(task => task.text.includes(searchText))} use later when we have categories
        form={form} setForm={setForm} destination={destination}>
        </FriendList> : <NoFriends/>}
    <button onClick={()=>getDistance(form['friends'], destination, 'driving')}>
      Calculate Distances</button>
    </FriendContainerStyle>
  )
}

const FriendList = ({destination, form, setForm}) => (
    <FriendListStyle>
        {form['friends'].map((task) => 
      <Friend key={task.id} {...task} destination={destination} form={form} setForm={setForm} />)}
    </FriendListStyle>
)

const Friend = ({id, name, address, distance, destination, form, setForm}) => { //replace with ...friend
    //const [places, setPlaces] = useState("");
    return(
      <FriendItem>
          <FriendName>{name}</FriendName> 
          <SearchBoxContainer value={form} setValue={(newPlace) => setForm({type: "NEW_ADDRESS", id: id, address: JSON.parse(newPlace).formatted_address, destination: destination})} 
            style={{height: "25px", padding: "0 0 0 20px", backgroundPosition: "0px", backgroundSize: "13px", width: "190px"}}>
            </SearchBoxContainer>
            {console.log(form)}
            <div style={{display:"flex", justifyContent:"space-between", padding:"0 5px 0 0"}}>
                <DeleteButton>
                <img src={DeleteImg} alt="Delete" style={{width: "15px"}} onClick={()=>setForm({friends: form['friends'].filter(friend => friend.id !== id)})}></img>
                </DeleteButton>
                <FriendName style={{margin: "-4px 0 0 0", fontSize: "15px"}}>{distance}</FriendName>
            </div>
      </FriendItem>
    )
  }

const NoFriends = () => (
<div>
  Add some friends! :)
</div>
)

export default FriendContainer